import { Component } from '@angular/core';
import { SettingService } from 'app/services/setting/setting.service';
// import * as dd from '../dingtalk.js';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title: String;
  isWeb: boolean;

  constructor(public settings: SettingService) {
    this.title = settings.getTitle();
    this.isWeb = (dd.version == null);
    if (this.isWeb) {
      setInterval(() => {
        if (this.title !== this.settings.getTitle()) {
          this.title = settings.getTitle();
        }
      }, 1000);
    }
    console.log(dd);
  }
}



